import React from "react";
import { AccountContent } from "./AccountContent";
import "./AccountModal.scss";
import { useDispatch, useSelector } from "react-redux";
import { set } from "../modules/redux/store";
import { handleAccountUpdate } from "../controllers/handleAccountUpdate";
import { selectGlobalAccount } from "../modules/redux/stateSelectors";
import { DeleteModal } from "./DeleteModal";

export function AccountModal() {
  const account = useSelector(selectGlobalAccount);
  const dispatch = useDispatch();

  if (!account) return <></>;

  return (
    <>
      <button
        id="accountModalButton"
        type="button"
        className="btn btn-secondary"
        data-bs-toggle="modal"
        data-bs-target="#accountModal"
      >
        Account
      </button>

      <form
        onSubmit={handleSubmit}
        className="modal fade"
        id="accountModal"
        tabIndex={-1}
        aria-labelledby="accountModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h1 className="modal-title fs-5" id="accountModalLabel">
                Account
              </h1>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <AccountContent />
            </div>
            <div className="modal-footer">
              <button
                id="accountCloseButton"
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
                name="closeButton"
              >
                Close
              </button>
              <button
                onClick={handleClick}
                type="button"
                className="btn btn-danger"
              >
                Delete
              </button>
              <button type="submit" className="btn btn-primary">
                Save
              </button>
            </div>
          </div>
        </div>
      </form>
    </>
  );

  async function handleSubmit(event: any) {
    event.preventDefault();
    const form = event.target;
    const inputs = form.elements;
    const newName = inputs.name.value;
    const newPhone = inputs.phone.value;
    const closeButton = inputs.closeButton;
    const data = {
      email: account.email,
      password: account.password,
      name: newName,
      phone: newPhone,
    };
    const updatedAccount = await handleAccountUpdate(data);
    if (updatedAccount) {
      let action = set.globalAccount(updatedAccount);
      dispatch(action);
      action = set.accountModalErrorMessage("");
      dispatch(action);

      closeButton.click();
      form.reset();
    } else {
      const action = set.accountModalErrorMessage(
        "There was an error updating your account."
      );
      dispatch(action);
    }
  }
}

function handleClick() {
  const closeButton = document.getElementById("accountCloseButton");
  closeButton.click();
  const openButton = document.getElementById("deleteModalButton");
  openButton.click();
}
