import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  selectDeleteModalErrorMessage,
  selectGlobalAccount,
} from "../modules/redux/stateSelectors";
import { handleAccountDelete } from "../controllers/handleAccountDelete";
import { set } from "../modules/redux/store";
import { DeleteContent } from "./DeleteContent";
import "./DeleteModal.scss";

export function DeleteModal() {
  const account = useSelector(selectGlobalAccount);
  const dispatch = useDispatch();

  return (
    <>
      <button
        id="deleteModalButton"
        type="button"
        className="btn btn-danger"
        data-bs-toggle="modal"
        data-bs-target="#deleteModal"
      >
        Delete
      </button>

      <form
        onSubmit={handleSubmit}
        className="modal fade"
        id="deleteModal"
        tabIndex={-1}
        aria-labelledby="deleteModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-header">
              <h1 className="modal-title fs-5" id="deleteModalLabel">
                Delete Account
              </h1>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              <DeleteContent />
            </div>
            <div className="modal-footer">
              <button
                name="closeButton"
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
              <button type="submit" className="btn btn-danger">
                Delete
              </button>
            </div>
          </div>
        </div>
      </form>
    </>
  );

  async function handleSubmit(event: any) {
    event.preventDefault();
    const form = event.target;
    const inputs = form.elements;
    const password = inputs.password.value;
    const email = account.email;
    const didDelete = await handleAccountDelete(email, password);
    if (didDelete) {
      const closeButton = inputs.closeButton;
      closeButton.click();
      const action = set.globalAccount(undefined);
      dispatch(action);
      localStorage.setItem("credentials", "");
    } else {
      const action = set.deleteModalErrorMessage(
        "Unable to delete the account"
      );
      dispatch(action);
    }
  }
}
